<template>
  <Column class="p-lr-40 p-tb-28">
    <!-- header -->
    <Row class=" w-100b p-b-40">
      <Column class="w-236 h-236 m-r-40">
        <img class="w-236 h-236" src="@/image/downLoad/downLoad_logo.webp">
      </Column>
      <Column>
        <Row class="font-44 bold">{{ $t('downLoad.comTitile') }}</Row>
        <Row class="c-info font-28">{{ $t('downLoad.subTitle1') }}</Row>

        <Row v-if="device === 'android'" class="c-white no-andriod m-t-22" x="c" y="c">{{ $t('downLoad.subTitle2') }}</Row>
        <Column v-else class="m-t-22">
          <el-button v-if="isDowning === 1" type="primary" class="w-172 h-56 radius-28 font-28" @click="toDown">{{ $t('downLoad.downNow') }}</el-button>
          <div v-else-if="isDowning === 2" class="demo-progress ">
            <el-progress :text-inside="true" :stroke-width="28" :percentage="percentage" />
          </div>
          <el-button v-if="isDowning === 3" type="primary" class="w-172 h-56 radius-28 font-28">{{ $t('downLoad.downFinished') }}</el-button>
        </Column>

      </Column>
    </Row>

    <!-- tips -->
    <Column class="w-100b p-lr-20 p-tb-28 dl-tips radius-8">
      <Column class="w-100b">
        <Row class="m-b-16">
          <img class="w-38 h-38" src="@/image/downLoad/warn.webp">
          <Row class="font-30 bold c-dark m-l-20">{{ $t('downLoad.tips1') }}</Row>
        </Row>
        <span class="w-100b font-28 bold f-wrap" y="c">
          <span class="c-info">{{ $t('downLoad.tips2') }}</span>
          <!-- <span class="w-40 h-40 m-lr-8">12</span> -->
          <img class="w-40 m-lr-4" src="@/image/downLoad/seting.webp">
          <span class="c-primary">{{ $t('downLoad.tips3') }}</span>
          <span class="c-primary w-100b font-28 bold" y="c">{{ $t('downLoad.tips4') }}</span>
        </span>
      </Column>
    </Column>
    <!-- update content -->
    <!-- <Column class="border-b w-100b p-b-40 m-t-45">
      <Row class="c-dark font-44 bold">{{ $t('downLoad.updateTitle') }}</Row>
      <Row class="c-info font-30 m-t-8">{{ $t('downLoad.updateVerison') }}</Row>
      <Row class="c-dark font-30 m-t-20 bold">{{ $t('downLoad.updateTips1') }}</Row>
    </Column> -->

    <!-- info -->
    <Column class="m-t-40 w-100b">
      <Row class="font-44 c-dark bold">{{ $t('downLoad.info') }}</Row>
      <Row class="border-b w-100b p-tb-28" x="sb">
        <div class="c-info font-30">{{ $t('downLoad.info2') }}</div>
        <div class="c-dark font-30 bold">{{ $t('downLoad.comTitile') }}</div>
      </Row>
      <Row class="border-b w-100b p-tb-28" x="sb">
        <div class="c-info font-30">{{ $t('downLoad.iosSize') }}</div>
        <div class="c-dark font-30 bold">{{ $t('downLoad.iosSizeCon') }}</div>
      </Row>
      <Row class="border-b w-100b p-tb-28" x="sb">
        <div class="c-info font-30">{{ $t('downLoad.ages') }}</div>
        <div class="c-dark font-30 bold">{{ $t('downLoad.agesLimit') }}</div>
      </Row>
      <Row class="border-b w-100b p-tb-28" x="sb">
        <div class="c-info font-30">{{ $t('downLoad.price') }}</div>
        <div class="c-dark font-30 bold">{{ $t('downLoad.free') }}</div>
      </Row>
    </Column>
  </Column>
</template>
<script setup>
let percentage = $ref(0)

let isDowning = $ref(1) // 1 未开始 2下载中 3下载完成

const toDown = () => {
  const url = `itms-services://?action=download-manifest&url=${iosDdUrl}`
  window.open(url, '_blank')
  isDowning = 2
  percentage = 0
  setPercentage()
}

const device = $computed(() => {
  const ran = navigator.userAgent
  const isAndroid = ran.indexOf('Android') > -1 || ran.indexOf('Linux') > -1
  const isIOS = !!ran.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
  if (isAndroid) return 'android'
  if (isIOS) return 'ios'
  return ''
})

const setPercentage = () => {
  const setIntervalId = setInterval(() => {
    const dur = Math.random() * 10 + 3
    percentage = percentage + Math.ceil(dur)
    if (percentage > 100) {
      percentage = 100
      isDowning = 3
      clearInterval(setIntervalId)
    }
  }, 4000)
}

onMounted(() => {
})

</script>
